import * as React from "react";
import Header from "./header";
import { appContext } from "../hooks/provider";
import LoginView from "./login";
import Footer from "./footer";
import { SEO } from "./seo";
import { CookieBanner, UpdatesBanner } from "./atoms";
import { updates } from "../data/updates";
import { v4 as uuidv4 } from "uuid";

type Props = {
  title: string;
  link: string;
  children?: React.ReactNode;
  showHeader?: boolean;
  restricted?: boolean;
  meta?: any;
};

const Layout = ({
  meta,
  title,
  link,
  children,
  showHeader = true,
  restricted = false,
}: Props) => {
  React.useEffect(() => {
    const gtagID = process.env.GATSBY_GTAG_ID;
    const GA_LOCAL_STORAGE_KEY = "ga:clientId";
    const gaClientId = window.localStorage.getItem(GA_LOCAL_STORAGE_KEY);
    if (!gaClientId) {
      const newClientId = uuidv4();
      window.localStorage.setItem(GA_LOCAL_STORAGE_KEY, newClientId);
    }

    console.log("gaClientId", gaClientId);

    if (gtagID && typeof window !== "undefined") {
      const script = document.createElement("script");
      script.src = `https://www.googletagmanager.com/gtag/js?id=${gtagID}`;
      script.async = true;
      document.body.appendChild(script);
      const script2 = document.createElement("script");
      script2.innerHTML = `window.dataLayer = window.dataLayer || [];
      function gtag(){dataLayer.push(arguments);}
      
      gtag('js', new Date());
      gtag('config', '${gtagID}', { 'anonymize_ip': true, 'client_storage': 'none', client_id: '${gaClientId}', send_page_view: true });  
      `;
      document.body.appendChild(script2);
      console.log("gtag added");
    }
  }, []);

  const LayoutView = ({ darkMode }: { darkMode: string }) => {
    React.useEffect(() => {
      document.getElementsByTagName("html")[0].className = `${
        darkMode === "dark" ? "dark bg-primary" : "light bg-primary"
      } `;
    }, [darkMode]);

    return (
      <div className={` flex flex-col`}>
        <div className="flex-1 text-primary">
          {/* <title>{meta?.title + " | " + title}</title> */}
          <SEO
            title={meta?.title + " | " + title}
            description={meta?.description}
          />
          {showHeader && <Header meta={meta} link={link} />}

          <UpdatesBanner>
            <span>
              <span className="font-semibold mx-1 inline-block">
                {updates[0]["date"]}
              </span>{" "}
              {updates[0]["update"]}
            </span>
          </UpdatesBanner>
          {/* <CookieBanner /> */}
          <div>{children}</div>
        </div>
        <Footer />
      </div>
    );
  };
  return (
    <appContext.Consumer>
      {(context: any) => {
        if (restricted) {
          return (
            <div className="h-screen bg-yellow-100 ">
              {context.user && <LayoutView darkMode={context.darkMode} />}
              {!context.user && <LoginView meta={meta} />}
            </div>
          );
        } else {
          return <LayoutView darkMode={context.darkMode} />;
        }
      }}
    </appContext.Consumer>
  );
};

export default Layout;
